            sender: lineData.is_user ? 'user' : 'ai',
            contents: lineData.swipes || [lineData.mes],
            activeContentIndex: (lineData.swipes || [lineData.mes]).indexOf(lineData.mes),
            timestamp: getFormattedTimestamp(), // 
