// src/db.js
import Dexie from 'dexie';

//  'Geniu5Database' 
export const db = new Dexie('Geniu5Database');

//  "" (schema) 
db.version(1).stores({
  // 'characters' 
  // 'id' 
  characters: 'id', 
  
  // 'prompts' 
  // 'id' 
  prompts: 'id', 
  
  // 'apiConfigs' 
  // '++id'  id 'name' 
  apiConfigs: '++id, name', 
  
  // 'kvStore' (Key-Value Store) 
  // 
  // 'key' 
  kvStore: 'key',
});